.search-content {
  text-align: left;
  margin-left: 5%;
  margin-right: 10em;
  margin-top: 1em;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  padding: 0.2em 2em 2em 2em;
  background-color: #4fa2be;
  border-width: 2px;
  border-style: inset;
}

.form-content {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.form-item {
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 50%;
  height: auto;
  padding: 5px;
}

.form-title {
  width: 100%;
  margin-bottom: 1.5em;
}

@media (max-width: 777px) {
  .form-content {
    display: block;
  }
  .form-item {
    width: 100%;
    margin: 0 0 0.5em 0;
  }
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



